import request from '@/utils/request'
// 获取验证码
export function getCode(phone) {
    return request({
        url: '/api/Login/sendCode',
        method: 'post',
        data:{ phone }
    })
}  
export function userRegister(data){
    return request({
        url: '/checkin/user/userRegister',
        method: 'post',
        data
    })
} 
export function discernID(data){
    return request({
        url: '/checkin/user/discernID',
        method: 'post',
        data
    })
} 
export function upload(data){
    return request({
        url: '/checkin/user/upload',
        method: 'post',
        data
    })
} 
// 登录
export function resetPassword(params){
    return request({
        url: '/api/login/resetPassword',
        method: 'post',
        params:params
    })
} 
export function userLogin(phone,phone_code){
    return request({
        url: '/api/Login/checkLogin',
        method: 'post',
        data:{ phone,phone_code}
    })
} 
export function apply(data){
    return request({
        url: '/api/apply/apply',
        method: 'post',
        data
    })
} 
export function quit(params){
    return request({
        url: '/api/index/quit',
        method: 'post',
        params:params
    })
} 
export function reg(params){
    return request({
        url: '/api/login/register',
        method: 'post',
        params:params
    })
} 