        { name: "铁门支行" },
        { name: "李村支行" },
        { name: "磁涧支行" },
        { name: "五头支行" },
        { name: "正村支行" },
        { name: "仓头支行" },
        { name: "石井支行" },
        { name: "北冶支行" },
        { name: "石寺支行" },
        { name: "青要山支行" },
        { name: "高平分理处" }
      ],
      jear: [
        { name: "10万以内" },
        { name: "10万以上30万以内" },
