        { name: "变更手机号" },
        { name: "重置登录密码" },
        { name: "吞卡处理" }
      ],
      reduceList: [],
      abc: "2",
      abcd: "2",
      adg: "2",
      ddw: "2",
      adliu: true,
      image: "",
      img: "",
      idcard: "",
      company: "",
      animal_heat: "",
      address: "",
      code: ""
    };
  },
  created() {
    if (this.$route.query.code) {
      this.code = this.$route.query.code;
      localStorage.setItem("code", this.code);
    }
    // if (localStorage.getItem("img")) {
    //    let data = {
    //         path:localStorage.getItem("img"),
    //         // path:'../imgs/11.jpg'
    //   };
    //       discernID(data).then(res => {
    //       this.idcard = res.idcard;
    //       this.name = res.name;
    //       this.address = res.address;
    //       this.phone = res.phone;
    //       this.company = res.company;
    //       this.animal_heat = res.animal_heat;
    //       this.remark = res.remark;
    //       if(res.type==1){
    //       this.a=true

    //       }else{
    //       this.a=false

    //       }
    //     });
    // }
    // if (localStorage.getItem("name")) {
    //   this.address = localStorage.getItem("address");
    //   this.name = localStorage.getItem("name");
    //   this.phone = localStorage.getItem("phone");
    //   this.idcard = localStorage.getItem("idcard");
    //   this.remark = localStorage.getItem("remark");
    //   this.company = localStorage.getItem("company");
    //   this.animal_heat = localStorage.getItem("animal_heat");
    // }
    // if (localStorage.getItem("phone")) {
    //   this.address = localStorage.getItem("address");
    //   this.name = localStorage.getItem("name");
    //   this.phone = localStorage.getItem("phone");
