    // }
  },
  methods: {
    idcardinput() {
      localStorage.setItem("idcard", this.idcard);
    },
    nameinput() {
      localStorage.setItem("name", this.name);
    },
    addressinput() {
      localStorage.setItem("address", this.address);
    },
    phoneinput() {
      localStorage.setItem("phone", this.phone);
    },
    animalinput() {
      localStorage.setItem("animal_heat", this.animal_heat);
    },
    companyinput() {
      localStorage.setItem("company", this.company);
    },
    remarkinput() {
      localStorage.setItem("remark", this.remark);
    },
    getBase64(file) {
      return new Promise(function(resolve, reject) {
        let reader = new FileReader();
        let imgResult = "";
        reader.readAsDataURL(file);
        reader.onload = function() {
          imgResult = reader.result;
        };
        reader.onerror = function(error) {
          reject(error);
        };
        reader.onloadend = function() {
          resolve(imgResult);
        };
      });
    },
    upImg(file) {
      //   console.log(file)
      //      this.getBase64(file.file).then(res => {
      //   let isJPG = res.split(";")[0].split("/")[1];
      //   if (isJPG == "jpeg" || isJPG == "jpg" || isJPG == "png") {
      //     this.image = res.split(",")[1];
      //     console.log(this.image,123,res)
      //           let data={
      //   img:this.image
      // }
      //     upload(this.image, "goods").then(resp => {

      //     });
      //   } else {
      //     this.$message.error("上传图片只能是正确的图片格式!");
      //     return;
      //   }
      // });
       
      this.image = file.content.substring(file.content.indexOf(",") + 1);
      let data = {
        img: this.image
      };
      upload(data).then(resp => {
           this.$toast.loading({
      mask: true,
      duration: 500,
      message: "正在识别身份证..."
    });
        this.img = resp.img;
        let dat = {
          path: this.img
        };
        discernID(dat).then(res => {
        this.$toast.clear();
         this.$toast(res.msg);
         console.log(res)
          if(res.res.type==1){
          this.a=true
            this.idcard = res.res.idcard;
          this.name = res.res.name;
          this.address = res.res.address;
          this.phone = res.res.phone;
          this.company = res.res.company;
          this.animal_heat = res.res.animal_heat;
          this.remark = res.res.remark;
          }else{
          this.a=false
          this.name = res.res.name;
          this.address = res.res.address;
            this.idcard = res.res.idcard;
          }
          // localStorage.setItem("address", this.address);
          // localStorage.setItem("name", this.name);
          // localStorage.setItem("idcard", this.idcard);
        });
      });
    },
    endTim(e) {
      this.endTimePop = false;
    },
    formatter(type, val) {
      if (type === "year") {
        return `${val}年`;
      } else if (type === "month") {
        return `${val}月`;
      } else if (type === "day") {
        return `${val}日`;
      }
      return val;
    },
    endTimePo() {
      if (this.abcd == 1) {
        this.endTimePop = true;
      }
    },
    namer() {
      this.b = false;
    },
    fouce() {
      if (this.phone == "") {
        this.$toast("请输入电话号码");

        this.ddw = 2;
      }
      if (!/^1[3456789]\d{9}$/.test(this.phone)) {
        this.$toast("电话号码格式错误");
        this.ddw = 2;
      }
    },
    phoner() {
      this.ddw = 1;
    },
    onChange(index) {
      this.current = index;
    },
    shower() {
      if (this.ddw == 1) {
        this.show2 = true;
        this.abc = 1;
      }
    },
    qwershw() {
      this.show2 = false;
      this.abc = 2;
    },
    qwer() {
      this.show1 = false;
      this.abcd = 2;
      this.adliu = false;
    },
    shower1() {
      if (this.abc == 1) {
        this.show1 = true;
        this.adliu = false;
        this.abcd = 1;
      }
    },
    submit() {
      if(this.name==''||this.phone==''||this.address==''||this.animal_heat==''||this.idcard==''){
        Toast("请完善信息");  
      }
        //   var idcardReg = /^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/;
        // if(idcardReg.test(this.idcard)) {
        //       Toast("身份证号码有误，请重填");  
        //         return false; 
        // }
    //   if(!(/^1[3456789]\d{9}$/.test(this.phone))){ 
    //     Toast("手机号码有误，请重填");  
    //     return false; 
    // } 

      this.type = this.list.toString();
      this.bank_type = this.backlist.toString();
      let data = {
        name: this.name,
        phone: this.phone,
        code: this.code,
        address: this.address,
        idcard: this.idcard,
        animal_heat: this.animal_heat,
        company: this.company,
        remark: this.remark
      };
      userRegister(data).then(res => {
        if (res.errcode == 0) {
          Toast({
            message: res.msg,
            duration: 3 * 1000
          });
         localStorage.setItem("img",this.img);

          this.name = "";
          this.phone = "";
          this.address = "";
          this.idcard = "";
          this.animal_heat = "";
          this.company = "";
          this.remark = "";
        } else {
          Toast({
            message: res.msg,
            duration: 3 * 1000
          });
        }
      });
    },
    cancelPay() {
      this.je = "";
      this.yt = "";
      this.integralShow = false;
      this.abc = 2;
    },
    qitacancelPay() {
      this.qitashow = false;
      this.qita = "";
      this.abc = 2;
    },
    qitapay() {
      this.decript = this.qita;
    },
    cunkuancancelPay() {
      this.cunkuanshow = false;
      this.cunkuan = "";
      this.abc = 2;
    },
    cunkuanpay() {
      this.deposit = this.cunkuan;
    },
    qukuancancelPay() {
      this.qukuanshow = false;
      this.abc = 2;
      this.qukuan = "";
    },
    qukuanpay() {
      this.get = this.qukuan;
    },
    dzcancelPay() {
      this.dzshow = false;
      this.dz = [];
      this.abc = 2;
    },
    dzpay() {
      // this.bank_type=this.dz.join(',')
      var art = [];
      this.dz.filter((i, x) => {
        art.push(this.actions3[i].name);
        this.backlist = art;
      });
      // this.get= this.dz
    },

    pay() {
      this.loan = this.jear[this.je].name;
      this.use_loan = this.ytar[this.yt].name;

      this.integralShow = false;
    },
    delWallet(id) {
      this.id = id;
    },
    endTimeChange(e) {
      let endTimeArr = e.getValues();
      this.end_time = `${endTimeArr[0]}-${endTimeArr[1]}-${endTimeArr[2]}  `;
    },
    onSelect(item) {
      this.typename = item.name;
      this.show = false;
    },
    onSelect1(item) {
      this.istype = item.name;
      if (this.istype == "否") {
        this.is_urgency = 0;
      } else if (this.istype == "是") {
        this.is_urgency = 1;
      }
      this.show1 = false;
    },
    onSelect2(item) {
      this.typesname = item.name;
      if (this.typesname == "存款") {
        this.type = 1;
        this.cunkuanshow = true;
      } else if (this.typesname == "取款") {
        this.type = 2;
        this.qukuanshow = true;
      } else if (this.typesname == "贷款") {
        this.type = 3;
        this.integralShow = true;
      } else if (this.typesname == "电子银行") {
        this.type = 4;
        this.dzshow = true;
      } else if (this.typesname == "其他") {
        this.type = 5;
        this.qitashow = true;
      }
      this.list.push(this.type);
      this.list = Array.from(new Set(this.list));
      this.show2 = false;
    },
    onSelect3(item) {
      if (item.name == "开通手机银行") {
        this.bank_type = 1;
      } else if (item.name == "开通网上银行") {
        this.bank_type = 2;
      } else if (item.name == "变更手机号") {
        this.bank_type = 3;
      } else if (item.name == "重置登录密码") {
        this.bank_type = 4;
      } else if (item.name == "吞卡处理") {
        this.bank_type = 5;
      }
      this.integral = false;
    },
    showtime() {
      this.showtime = true;
    },
    showweek() {
      if (this.abcd == 1) {
        this.show = true;
        this.adg = 1;
      }
    },
    //   onSelect(item) {
    //   // 默认情况下点击选项时不会自动收起
    //   // 可以通过 close-on-click-action 属性开启自动收起
    //   this.show = false;
    //   Toast(item.name);
    // },
    go(res) {
      this.$router.push({ name: res });
    },
    goPay() {
      this.$router.push({
        path: "payOrder",
        query: {
          type: 1
        }
      });
    }
